<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="warp-main" v-loading="loading" element-loading-text="">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="filters">
          <el-form-item>
            <el-button type="primary" @click="showAddDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <el-table :data="list" highlight-current-row @selection-change="selsChange"
                style="width: 100%;">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column type="index" width="60"></el-table-column>
        <el-table-column prop="product" label="" width="100"></el-table-column>
        <el-table-column prop="number" label="" width="100" sortable></el-table-column>
        <el-table-column prop="firstPrice" label="" width="100" sortable></el-table-column>
        <el-table-column prop="secordPrice" label="" width="100" sortable></el-table-column>
        <el-table-column prop="productPic" label="ICON" width="100">
          <template slot-scope="scope">
            <img :src="scope.row.productPic" alt="" style="width: 50px;height: 50px;">
          </template>
        </el-table-column>
        <el-table-column prop="payType" label="" width="100"></el-table-column>
        <el-table-column prop="sale" label="" width="100" sortable></el-table-column>
        <el-table-column prop="weight" label="" width="100" sortable></el-table-column>
        <el-table-column prop="remainSecond" label="">
          <template slot-scope="scope">
            <el-button size="small" @click="deleteDialog(scope.$index,scope.row)"></el-button>
            <el-button size="small" @click="adjustDialog(scope.$index,scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-button type="danger" @click="batchDeleteBook" :disabled="this.sels.length===0"></el-button>
        <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                       style="float:right;">
        </el-pagination>
      </el-col>

      <!---->
      <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
      <el-form :model="normalForm" label-width="100px" :rules="rules" class="demo-ruleForm">
        <el-form-item label="" prop="product">
          <el-select v-model="normalForm.product" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="number" style="width:317px;">
          <el-input v-model="normalForm.number" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="payType">
          <el-select v-model="normalForm.payType" placeholder="">
            <el-option
              v-for="item in payment"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="firstPrice">
          <el-input v-model="normalForm.firstPrice" auto-complete="off" style="width:217px;"></el-input> /
        </el-form-item>
        <el-form-item label="" prop="secordPrice">
          <el-input v-model="normalForm.secordPrice" auto-complete="off" style="width:217px;"></el-input> /
        </el-form-item>
        <el-form-item label="" prop="weight">
          <el-input v-model="normalForm.weight" auto-complete="off" style="width:217px;" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="ICON" prop="productPic">
          <div class="upload">
            <el-upload
              class="avatar-uploader"
              :action=domain
              :http-request=upqiniu
              :show-file-list="false"
              :before-upload="beforeUpload">
              <img v-if="imageUrl" :src="imageUrl" class="avatar">
              <el-button v-else class="el-icon-plus avatar-uploader-icon"></el-button>
            </el-upload>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="issue()"></el-button>
          <el-button type="primary" @click.native="addFormVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

      <!---->
      <el-dialog title="" :visible.sync="adjustmentVisible" :close-on-click-modal="false">
        <el-form :model="adjustForm" label-width="100px" :rules="rules" class="demo-ruleForm">
          <el-form-item label="" prop="product">
            <el-select v-model="adjustForm.product" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="number" style="width:317px;">
            <el-input v-model="adjustForm.number" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="payType">
            <el-select v-model="adjustForm.payType" placeholder="">
              <el-option
                v-for="item in payment"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="firstPrice">
            <el-input v-model="adjustForm.firstPrice" auto-complete="off" style="width:217px;"></el-input> /
          </el-form-item>
          <el-form-item label="" prop="secordPrice">
            <el-input v-model="adjustForm.secordPrice" auto-complete="off" style="width:217px;"></el-input> /
          </el-form-item>
          <el-form-item label="" prop="weight">
            <el-input v-model="adjustForm.weight" auto-complete="off" style="width:217px;"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-radio v-model="radioData" label="true"></el-radio>
            <el-radio v-model="radioData" label="false"></el-radio>
          </el-form-item>
          <el-form-item label="ICON" prop="productPic">
            <div class="upload">
              <el-upload
                class="avatar-uploader"
                :action=domain
                :http-request=upqiniu
                :show-file-list="false"
                :before-upload="beforeUpload">
                <img v-if="imageUrl" :src="imageUrl" class="avatar">
                <el-button v-else class="el-icon-plus avatar-uploader-icon"></el-button>
              </el-upload>
            </div>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="Adjust()"></el-button>
            <el-button type="primary" @click.native="adjustmentVisible = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

    </el-col>
  </el-row>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "MallManagement",
      data(){
          return{
            addFormVisible:false,
            loading:false,
            adjustmentVisible:false,
            total:0,
            list:[],
            img:true,
            normalForm:{},
            normalForm1:{},
            adjustForm:{},
            adjustForm1:{},
            filters: {},
            radioData:'',
            // 
            domain: 'http://up.qiniu.com',
            // 
            qiniuaddr: 'qiniu.3cscy.com',
            rules: {
              title: [
                {required: true, message: '', trigger: 'blur'}
              ],
              file: [
                {required: true, message: '', trigger: 'blur'}
              ],
              address: [
                {required: true, message: '', trigger: 'blur'}
              ]
            },
            imageUrl: '',
            options:[
              {value:''},
              {value:''},
              {value:''},
              {value:''}
            ],
            payment:[
              {value:''},
              {value:''}
            ],
            sels: [],
          }
      },
      methods:{
        trim(str) {
          if(str != null){
            return str.replace(/(^\s+)|(\s+$)/g, "");
          }
        },
        // 
        upqiniu(req) {
          const config = {
            headers: {'Content-Type': 'multipart/form-data'}
          }
          let filetype = ''
          if (req.file.type === 'image/png') {
            filetype = 'png'
          } else {
            filetype = 'jpg'
          }
          // 
          const keyname = 'anbang' + Math.random() + '.' + filetype
          // token
          axios({
            method: 'post',
            url: this.global.mPath + '/mailctrl/uptoken',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params:{
              token:sessionStorage.getItem('token')
            }
          }).then(res => {
            const formdata = new FormData()
            formdata.append('file', req.file)
            formdata.append('token', res.data.data)
            formdata.append('key', keyname)
            // 
            axios.post(this.domain, formdata, config).then(res => {
              this.imageUrl = 'http://' + this.qiniuaddr + '/' + res.data.key
              this.img = false;
            })
          })
        },
        // 
        beforeUpload(file) {
          const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
          const isLt2M = file.size / 1024 / 1024 < 2
          if (!isJPG) {
            this.$message.error(' JPG !')
          }
          if (!isLt2M) {
            this.$message.error(' 2MB!')
          }
          return isJPG && isLt2M
        },
        handleSearch(page){
          axios({
            url:this.global.mPath + '/agent/queryagentclubcard',
            method:'post',
            params:{
              page:page,
              size:10,
              token:sessionStorage.getItem('token')
            }
          }).then((res) => {
            this.list = res.data.data.items;
            this.total = res.data.data.pageCount;
            for(let i = 0;i < this.list.length;i++){
              if(this.list[i].sale == true){
                this.list[i].sale = ''
              }else{
                this.list[i].sale = ''
              }
            }
          }).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          })
        },
        showAddDialog:function(){
          this.imageUrl = '';
          this.addFormVisible = true;
        },
        issue:function() {
          if(this.normalForm.payType == ''){
            this.normalForm1.payType = 'wxpay'
          }
          if(this.normalForm.payType == ''){
            this.normalForm1.payType = 'scorepay'
          }
          axios({
            method: 'post',
            url: this.global.mPath + '/agent/addagentclubcard',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              product:this.normalForm.product,
              number:this.trim(this.normalForm.number),
              firstPrice:this.normalForm.firstPrice,
              secordPrice:this.trim(this.normalForm.secordPrice),
              payType:this.normalForm1.payType,
              productPic:this.imageUrl,
              weight:this.normalForm.weight,
              token:sessionStorage.getItem('token')
            }
          }).then((res) => {
                if (res.data.success == false) {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                } else if (res.data.success == true) {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                  });
                  this.addFormVisible = false;//
                  this.handleSearch(1);
                }
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          });
        },
        selsChange: function (sels) {
          this.sels = sels;
        },
        handleCurrentChange(val) {
          this.page = val;
          this.handleSearch(this.page);
        },
        adjustDialog:function(index,row){
          this.imageUrl = '';
          this.adjustmentVisible = true;
          this.adjustForm = Object.assign({}, row);
          if(this.adjustForm.sale == ''){
            this.radioData = 'true';
          }
          if(this.adjustForm.sale == ''){
            this.radioData = 'false';
          }
          if(this.imageUrl == ''){
            this.imageUrl = this.adjustForm.productPic;
          }
        },
        Adjust:function(){
          console.log(this.adjustForm.payType)
          if(this.adjustForm.payType == ''){
            this.adjustForm1.payType = 'wxpay'
          }
          if(this.adjustForm.payType == ''){
            this.adjustForm1.payType = 'scorepay'
          }
            this.loading = true;
            axios({
              method: 'post',
              url: this.global.mPath + '/agent/updateagentclubcard',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                id:this.adjustForm.id,
                product:this.adjustForm.product,
                number:this.adjustForm.number,
                firstPrice:this.adjustForm.firstPrice,
                secordPrice:this.adjustForm.secordPrice,
                payType:this.adjustForm1.payType,
                productPic:this.imageUrl,
                sale:this.radioData,
                weight:this.adjustForm.weight,
                token:sessionStorage.getItem('token')
              }
            })
              .then((res) => {
                  this.loading = false;
                  if (res.data.success == true) {
                    this.adjustmentVisible = false;
                    this.$message.success({showClose: true, message: '', duration: 1500});
                    this.handleSearch(1);
                  } else {
                    this.$message.error({showClose: true, message: err.toString(), duration: 2000});
                  }
                },
              ).catch((e) => {
              this.loading = false;
              this.$message.error({showClose: true, message: '', duration: 2000});
            });
          },
        deleteDialog:function(index,row){
          this.loading = true;
          axios({
            method: 'post',
            url: this.global.mPath + '/agent/deleteagentclubcard',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'cardId': row.id,
              'token':sessionStorage.getItem('token')
            }
          }).then((res) => {
              this.loading = false;
              if (res.data.success == true) {
                this.$message.success({showClose: true, message: '', duration: 1500});
                this.handleSearch(1);
              } else {
                this.$message.error({showClose: true, message: err.toString(), duration: 2000});
              }
            },
          ).catch((e) => {
            this.loading = false;
            this.$message.error({showClose: true, message: '', duration: 2000});
          });
        },
        batchDeleteBook:function(){
          let ids = this.sels.map(item => item.id).toString();
          let that = this;
          this.$confirm('', '', {
            type: 'warning'
          }).then(() => {
            that.loading = true;
            axios({
              method: 'post',
              url: this.global.mPath + '/agent/deleteagentclubcard',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                'cardId': ids,
                'token':sessionStorage.getItem('token')
              }
            })
              .then((res) => {
                  that.loading = false;
                  if (res.data.success == true) {
                    that.$message.success({showClose: true, message: '', duration: 1500});
                    that.handleSearch(1);
                  } else {
                    that.$message.error({showClose: true, message: err.toString(), duration: 2000});
                  }
                },
              ).catch((e) => {
              that.loading = false;
              that.$message.error({showClose: true, message: '', duration: 2000});
            });
          });
        }
      },
      mounted(){
        this.handleSearch()
      }
    }
</script>

<style scoped>
  .toolbar{
    margin-top:30px;
  }
  p{
    text-align: center;
    font-size:18px;
  }
  .avatar{
    width:100px;
    height:100px;
  }
</style>



