<script>
    import axios from 'axios'
    export default {
        name: "PromoterManagement",
        data(){
          return{
            filters: {},
            record:[],
            form:{},
            roles:[],
            total:0,
            upgrade:'',
            options: [
              {value: ''},
              {value: ''}
            ],
            types:[
              {value:''},
              {value:''},
              {value:''},
              {value:''},
            ],
            centerDialogVisible:false,
            relieveDialogVisible:false,
            LevelUpVisible:false,
            boundVisible:false,
            operateVisible:false,
            disqualifyDialogVisible:true,
            unlockingDialogVisible:false,
            publishVisible:false,
            notarizeVisible:false,
            integralVisible:false,
            sureVisible:false,
            codeVisible:false,
            playersVisible:false,
            other:false,
            state:'',
            seniorAmount:'',
            juniorAmount:'',
            sorting:{},
            page:1,
            state1:{},
            publishForm:{},
            number:'',
            id:'',
            integralForm:{},
            score:'',
            publishForm1:{},
            src:'',
            player:[],
            details:{},
            tableData:[]
          }
        },
        methods:{
          dateTimeFormat(value) {
            let time = new Date(+value);
            let rightTwo = (v) => {
              v = '0' + v;
              return v.substring(v.length - 2, v.length)
            };
            if (time == null) return;
            let year = time.getFullYear();
            let month = time.getMonth() + 1;
            let date = time.getDate();
            let hours = time.getHours();
            let minutes = time.getMinutes();
            let seconds = time.getSeconds();
            return year + '-' + rightTwo(month) + '-' + rightTwo(date) + ' ' + rightTwo(hours) + ':' + rightTwo(minutes) + ':' + rightTwo(seconds);
          },
          handleSearch(levelSort,createTimeSort,stateSort){
            if(this.filters.mailType == ''){
              this.filters.mailType1 = 1;
            }
            if(this.filters.mailType == ''){
              this.filters.mailType1 = 2;
            }
            if(this.filters.startTime){
              let date = new Date(this.filters.startTime);
              this.state1.startTime = date.getTime();
            }
            if(this.filters.endTime){
              let date = new Date(this.filters.endTime);
              this.state1.endTime = date.getTime();
            }
            if(this.filters.startTime &&
              this.filters.endTime &&
              this.state1.endTime - this.state1.startTime < 0){
              return;
            }
            axios({
              url:this.global.mPath + '/agent/queryagent',
              method:'post',
              params:{
                page:this.page,
                size:10,
                token:sessionStorage.getItem('token'),
                id:this.filters.id,
                nickname:this.filters.nickname,
                phone:this.filters.phone,
                userName:this.filters.userName,
                startTime:this.state1.startTime,
                endTime:this.state1.endTime,
                level:this.filters.mailType1,
                levelSort:levelSort,
                createTimeSort:createTimeSort,
                stateSort:stateSort
              }
            }).then((res) => {
                console.log(res.data)
                this.juniorAmount = res.data.data.juniorAmount
                this.seniorAmount = res.data.data.seniorAmount
                this.record = res.data.data.agentList.items;
                this.total = res.data.data.agentList.pageCount;
                for(let i = 0;i < this.record.length;i++){
                  this.record[i].createTime = this.dateTimeFormat(this.record[i].createTime)
                  if(this.record[i].level == 1){
                    this.record[i].level = '';
                    this.record[i].bossId1 = this.record[i].id;
                  }
                  if(this.record[i].level == 2){
                    this.record[i].level = '';
                    this.record[i].bossId1 = this.record[i].bossId;
                  }
                }
              }
            ).catch((e) => {
              this.$message.error({showClose: true, message: '', duration: 2000});
            })
          },
          sort(a){
            this.sorting = a;
            if(this.sorting.prop == 'level'){
              if(this.sorting.order == 'ascending'){
                this.sorting.level = 'ASC'
              }
              if(this.sorting.order == 'descending'){
                this.sorting.level = 'DESC'
              }
            }
            if(this.sorting.prop == 'createTime'){
              if(this.sorting.order == 'ascending'){
                this.sorting.createTime = 'ASC'
              }
              if(this.sorting.order == 'descending'){
                this.sorting.createTime = 'DESC'
              }
            }
            if(this.sorting.prop == 'state'){
              if(this.sorting.order == 'ascending'){
                this.sorting.state = 'ASC'
              }
              if(this.sorting.order == 'descending'){
                this.sorting.state = 'DESC'
              }
            }
            this.handleSearch(this.sorting.level,this.sorting.createTime,this.sorting.state)
          },
          //
          particulars:function(index,row){
            this.centerDialogVisible = true;
            // console.log(row)
            axios({
              url:this.global.mPath + '/agent/agentdetail',
              method:'post',
              params:{
                agentId:row.id,
                token:sessionStorage.getItem('token')
              }
            }).then((res) => {
                console.log(res.data)
                row.clubCardYue = res.data.data.clubCardYue;
                row.clubCardJi = res.data.data.clubCardJi;
                row.clubCardZhou = res.data.data.clubCardZhou;
                row.clubCardRi = res.data.data.clubCardRi;
                row.score = res.data.data.score;
                this.roles = [Object.assign({}, row)];
                this.state = res.data.data.agent.state;
                this.id = res.data.data.agent.id;
                this.score = res.data.data.score;
            }).catch((e) => {
                this.$message.error({showClose: true, message: '', duration: 2000});
            })
          },
          //
          operation:function(index,row){
            this.operateVisible = true;
            this.boundVisible = true;
            this.LevelUpVisible = false;
            this.form = Object.assign({}, row);
          },
          //
          bound:function(){
            axios({
              url:this.global.mPath + '/agent/setboss',
              method:'post',
              params:{
                agentId:this.form.id,
                bossId:this.form.bossId1,
                token:sessionStorage.getItem('token')
              }
            }).then((res) => {
              console.log(res.data)
              if(res.data.success){
                this.$message.success({showClose: true, message: '', duration: 1500});
                this.operateVisible = false;
                this.handleSearch(1)
              }else{
                this.$message.error({showClose: true, message: err.toString(), duration: 2000});
              }
            }).catch((e) => {
              this.$message.error({showClose: true, message: '', duration: 2000});
            })
          },
          //
          unbound:function(){
            axios({
              url:this.global.mPath + '/agent/removeboss',
              method:'post',
              params:{
                agentId:this.form.id,
                token:sessionStorage.getItem('token')
              }
            }).then((res) => {
              console.log(res.data)
              if(res.data.success){
                this.$message.success({showClose: true, message: '', duration: 1500});
                this.operateVisible = false;
                this.handleSearch(1)
              }else{
                this.$message.error({showClose: true, message: err.toString(), duration: 2000});
              }
            }).catch((e) => {
              this.$message.error({showClose: true, message: '', duration: 2000});
            })
          },
          //
          pinless:function(){
            this.LevelUpVisible = false;
            this.boundVisible = true;
          },
          //
          levelup:function(){
            this.LevelUpVisible = true;
            this.boundVisible = false;
          },
          //
          makeSure:function(){
            if(this.form.upgrade == ''){
              this.upgrade = 1;
            }
            if(this.form.upgrade == ''){
              this.upgrade = 2;
            }
            axios({
              url:this.global.mPath + '/agent/setlevel',
              method:'post',
              params:{
                agentId:this.form.id,
                level:this.upgrade,
                token:sessionStorage.getItem('token')
              }
            }).then((res) => {
              console.log(res.data)
              if(res.data.success){
                this.$message.success({showClose: true, message: '', duration: 1500});
                this.operateVisible = false;
                this.handleSearch(1)
              }else{
                this.$message.error({showClose: true, message: err.toString(), duration: 2000});
              }
            }).catch((e) => {
              this.$message.error({showClose: true, message: '', duration: 2000});
            })
          },
          CancelSubmit:function(){
            axios({
              url:this.global.mPath + '/agent/ban',
              method:'post',
              params:{
                agentId:this.roles[0].id,
                token:sessionStorage.getItem('token')
              }
            }).then((res) => {
              console.log(res.data)
              if(res.data.success){
                this.unlockingDialogVisible = true;
                this.disqualifyDialogVisible = false;
                this.roles[0].state = res.data.data
                this.state = ''
                this.handleSearch(1)
              }else{
                this.$message.error({showClose: true, message: err.toString(), duration: 2000});
              }
            }).catch((e) => {
              this.$message.error({showClose: true, message: '', duration: 2000});
            })
          },
          relieveSubmit:function(){
            axios({
              url:this.global.mPath + '/agent/liberate',
              method:'post',
              params:{
                agentId:this.roles[0].id,
                token:sessionStorage.getItem('token')
              }
            }).then((res) => {
              console.log(res.data)
              if(res.data.success){
                this.unlockingDialogVisible = false;
                this.disqualifyDialogVisible = true;
                this.roles[0].state = res.data.data
                this.state = ''
                this.handleSearch(1)
              }else{
                this.$message.error({showClose: true, message: err.toString(), duration: 2000});
              }
            }).catch((e) => {
              this.$message.error({showClose: true, message: '', duration: 2000});
            })
          },
          handleCurrentChange(val){
            this.page = val;
            this.sort(this.sorting);
          },
          MembershipAdjustment: function () {
            this.centerDialogVisible = false;
            this.publishVisible = true;
          },
          //
          sure() {
            if(this.publishForm.product == ''){
              this.publishForm1.product = 'ri'
            }
            if(this.publishForm.product == ''){
              this.publishForm1.product = 'zhou'
            }
            if(this.publishForm.product == ''){
              this.publishForm1.product = 'yue'
            }
            if(this.publishForm.product == ''){
              this.publishForm1.product = 'ji'
            }
            axios({
              method: 'post',
              url: this.global.mPath + '/agent/clubcardmanager',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                'agentId': this.id,
                'card':this.publishForm1.product,
                'cardAmount':this.publishForm.afternumber,
                'token':sessionStorage.getItem('token')
              }
            }).then((res) => {
                if (res.data.success == false) {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                } else if (res.data.success == true) {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                  });
                  this.centerDialogVisible = false;
                  this.notarizeVisible = false;
                  this.handleSearch();
                }
              },
            ).catch((e) => {
              if (e && e.response) {
                switch (e.response.status) {
                  case 504:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break
                  case 500:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break
                  case 405:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break
                }
              }
            });
          },
          IntegralAdjustment:function(){
            this.centerDialogVisible = false;
            this.integralVisible = true;
          },
          Confirm:function(){
            axios({
              method: 'post',
              url: this.global.mPath + '/agent/scoremanager',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                'agentId': this.id,
                'scoreAmount':this.integralForm.afterAmount,
                'token':sessionStorage.getItem('token')
              }
            }).then((res) => {
                if (res.data.success == false) {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                } else if (res.data.success == true) {
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                  });
                  this.centerDialogVisible = false;
                  this.sureVisible = false;
                  this.handleSearch();
                }
              },
            ).catch((e) => {
              if (e && e.response) {
                switch (e.response.status) {
                  case 504:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break
                  case 500:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break
                  case 405:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break
                }
              }
            });
          },
          Code:function(){
            this.centerDialogVisible = false;
            this.codeVisible = true;
            this.src = this.global.mPath + '/agent/qrcode?token='+sessionStorage.getItem('token')+'&agentId='+this.id
          },
          Players:function(page){
            this.playersVisible = true
            this.centerDialogVisible = false;
            axios({
              url:this.global.mPath + '/agent/queryinvitatemember',
              method:'post',
              params:{
                page:page,
                size:'10',
                agentId:this.id,
                token:sessionStorage.getItem('token')
              }
            }).then((res) => {
              console.log(res.data.data.listPage.items)
              this.player = res.data.data.listPage.items;
              for(let i = 0;i < this.player.length;i++){
                this.player[i].createTime = this.dateTimeFormat(this.player[i].createTime)
              }
            }).catch((e) => {
              this.$message.error({showClose: true, message: '', duration: 2000});
            })
          },
          //
          showother: function (index,row) {
            this.other = true;
            this.playersVisible = false;
            axios({
              method: 'post',
              url: this.global.mPath + '/member/querymemberdetail',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                'memberId': row.memberId,
                'token':sessionStorage.getItem('token'),
              }
            })
              .then((res) => {
                  this.loading = false;//
                  this.details = res.data.data;
                  this.tableData = res.data.data.roomList;
                  for(var i = 0;i < this.tableData.length;i++){
                    if(this.tableData[i].vip == true){
                      this.tableData[i].vip = ''
                    }else{
                      this.tableData[i].vip = ''
                    }
                  }
                },
              ).catch((e) => {
              if (e && e.response) {
                switch (e.response.status) {
                  case 504:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break
                  case 500:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break
                  case 405:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break
                }
              }
            });
            this.addForm = {
              name: '',
              author: '',
              publishAt: '',
              description: ''
            };
          },
        },
        mounted(){
            this.handleSearch();
        },
    }
</script>

<style scoped>
  .toolbar{
    margin-top:30px;
  }
  .img{
    float:left;
    margin-right:60px;
  }
  .left,
  .form{
    display:inline-block;
  }
  .left{
    position: absolute;
    top:60px;
  }
  .form{
    margin-left:140px;
  }
  .bound,
  .levelup{
    width:100px;
  }
  .levelup{
    margin-top:20px;
  }
  a{
    text-decoration: none;
    color:#fff;
  }
  .pic{
    width:50px;
    height:50px;
  }
  .bg-purple{
    width:200px;
    height:100px;
    text-align: center;
    background-color: #eee;
    border-radius: 5px;
    border:1px solid #eee;
  }
  .bg-purple div:nth-child(1){
    margin-top:20px;
    font-size:20px;
  }
  .bg-purple div:nth-child(2){
    margin-top:10px;
  }
  .el-button{
    padding:12px;
  }
</style>



