      }
    },
    methods: {
      trim(str) {
        if(str != null){
          return str.replace(/(^\s+)|(\s+$)/g, "");
        }
      },
      dateTimeFormat(value) {
        let time = new Date(+value);
        let rightTwo = (v) => {
          v = '0' + v;
          return v.substring(v.length - 2, v.length)
        };
        if (time == null) return;
        let year = time.getFullYear();
        let month = time.getMonth() + 1;
        let date = time.getDate();
        let hours = time.getHours();
        let minutes = time.getMinutes();
        let seconds = time.getSeconds();
        return year + '-' + rightTwo(month) + '-' + rightTwo(date) + ' ' + rightTwo(hours) + ':' + rightTwo(minutes) + ':' + rightTwo(seconds);
      },
      //
      showother: function (index, row) {
        sessionStorage.setItem('id', this.vip[index].id);//id
        //console.log(this.vip[index].id)
        axios({
          method: 'post',
          url: this.global.mPath + '/member/querymemberdetail',
          headers: {
            'Content-type': 'application/x-www-form-urlencoded'
          },
          params: {
            'memberId': row.id,
            'token':sessionStorage.getItem('token'),
          }
        })
          .then((res) => {
              this.loading = false;//
              this.details = res.data.data;
              this.tableData = res.data.data.roomList;
              for(var i = 0;i < this.tableData.length;i++){
                if(this.tableData[i].vip == true){
                  this.tableData[i].vip = ''
                }else{
                  this.tableData[i].vip = ''
                }
              }
            },
          ).catch((e) => {
          if (e && e.response) {
            switch (e.response.status) {
              case 504:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 500:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                this.loading = false;//
                break
              case 405:
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'warning'
                });
                break
            }
          }
        });
        this.other = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      },
      //
      givegold() {
        let ids = this.sels.map(item => item.id).toString();
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          if (this.normalForm.gold == undefined || this.normalForm.gold == "") {
            this.$message({
              showClose: true,
              message: '',
              type: 'warning'
            });
          } else if (this.normalForm.gold < 0) {
            this.$message({
              showClose: true,
              message: '',
              type: 'warning'
            });
          } else {
            that.loading = true;
            axios({
              method: 'post',
              url: this.global.mPath + '/member/give_reward',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                'gold': this.trim(this.normalForm.gold),
                'id': ids,
                'token':sessionStorage.getItem('token')
              }
            })
              .then((res) => {
                  that.loading = false;
                  if (res.data.success == true) {
                    that.$message.success({showClose: true, message: '', duration: 1500});
                    this.normalForm.gold = '';//
                    this.giveFormVisible = false;//
                  } else {
                    that.$message.error({showClose: true, message: err.toString(), duration: 2000});
                  }
                },
              ).catch((e) => {
              if (e && e.response) {
                switch (e.response.status) {
                  case 504:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break;
                  case 500:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break;
                  case 405:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break;
                  case 400:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    that.loading = false;
                    break;
                }
              }
            });
          }
        });
      },
      //
      showgold: function () {
        this.giveFormVisible = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      },
      //
      giveintegral() {
        let ids = this.sels.map(item => item.id).toString();
        console.log(ids)
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          if (this.normalForm.score == undefined || this.normalForm.score == "") {
            this.$message({
              showClose: true,
              message: '',
              type: 'warning'
            });
