                    that.$message.success({showClose: true, message: '', duration: 1500});
                    this.normalForm.score = '';//
                    this.givefromcard = false;//
                  } else {
                    that.$message.error({showClose: true, message: err.toString(), duration: 2000});
                  }
                },
              ).catch((e) => {
              if (e && e.response) {
                switch (e.response.status) {
                  case 504:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break;
                  case 500:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break;
                  case 405:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break;
                  case 400:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break;
                }
              }
            });
          }
        });
      },
      //
      showintegral: function (index, row) {
        this.givefromintegral = true;
        this.addForm = {
          name: '',
          author: '',
          publishAt: '',
          description: ''
        };
      },
      //
      showcard:function(){
        this.givefromcard = true;
      },
      givecard:function(){
        if(this.cardForm.mold == ''){
          this.cardForm1.mold = 1;
        }
        if(this.cardForm.mold == ''){
          this.cardForm1.mold = 7;
        }
        if(this.cardForm.mold == ''){
          this.cardForm1.mold = 30;
        }
        if(this.cardForm.mold == ''){
          this.cardForm1.mold = 90;
        }
        let ids = this.sels.map(item => item.id).toString();
        let that = this;
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          that.loading = true;
          axios({
            method: 'post',
            url: this.global.mPath + '/member/give_reward_clubcard',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'vipTime':this.cardForm1.mold,
              'id': ids,
              'token': sessionStorage.getItem('token')
            }
          }).then((res) => {
            that.loading = false;
            if (res.data.success == true) {
              that.$message.success({showClose: true, message: '', duration: 1500});
              this.cardForm.mold = '';//
              this.givefromcard = false;//
            } else {
              that.$message.error({showClose: true, message: err.toString(), duration: 2000});
            }
          }).catch((e) => {
