        </el-form-item>
        <el-form-item label="" prop="type" required>
          <el-select v-model="addForm.type" placeholder="">
            <el-option
              v-for="item in types"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="singleNum" required>
          <el-input v-model="addForm.singleNum" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="storeNum" required>
          <el-input v-model="addForm.storeNum" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="icon" prop="iconUrl" required>
          <div class="upload">
            <el-upload
              class="avatar-uploader"
              :action=domain
