        <el-button type="primary" @click.native="tip3Visible = false" style="margin:30px 0 0 55px;"></el-button>
      </div>
    </el-dialog>

    <!--10-->
    <el-dialog title="" :visible.sync="tip4Visible" :close-on-click-modal="false" width="400px" class="tip">
      <div class="title"></div>
      <div class="content1" style="text-align: center;">10!</div>
      <div class="footer">
        <el-button @click.native="tip4Visible = false" style="margin:30px 0 0 80px;"></el-button>
