</template>

<script>
    import axios from 'axios'
    export default {
        name: "Lottery",
        data(){
          return{
            adjustmentVisible:false,
            addVisible:false,
            imageUrl: '',
            imageUrl1: '',
            addFormRules: {
              name: [
                {required: true, message: '', trigger: 'blur'}
              ],
              type:[
                {required: true, message: '', trigger: 'change'}
              ],
              singleNum: [
                {required: true, message: '', trigger: 'blur'}
              ],
              storeNum: [
                {required: true, message: '', trigger: 'blur'}
              ],
              prizeProb: [
                {required: true, message: '', trigger: 'blur'}
              ],
              firstPrizeProb: [
                {required: true, message: '', trigger: 'blur'}
              ],
              overstep:[
                {required: true, message: '', trigger: 'change'}
              ]
            },
            adjustForm:{},
            addForm:{},
            items:[
              {value:''},
              {value:''},
              {value:''},
              {value:''},
              {value:''},
              {value:''},
            ],
            types:[
              {value:''},
              {value:''},
              {value:''},
              {value:''},
              {value:''},
            ],
            jackpot:[
              {value:''},
              {value:''},
            ],
            lottery:[],
            filters:{},
            // 
            domain: 'http://up.qiniu.com',
            // 
            qiniuaddr: 'qiniu.3cscy.com',
            tipVisible:false,
            tip1Visible:false,
            tip2Visible:false,
            tip3Visible:false,
            tip4Visible:false,
            previewVisible:false,
          }
        },
      methods:{
        // 
        upqiniu(req) {
          console.log(req)
          const config = {
            headers: {'Content-Type': 'multipart/form-data'}
          }
          let filetype = ''
          if (req.file.type === 'image/png') {
            filetype = 'png'
          } else {
            filetype = 'jpg'
          }
          // 
          const keyname = 'anbang' + Math.random() + '.' + filetype
          // token
          axios({
            method: 'post',
            url: this.global.mPath + '/mailctrl/uptoken',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params:{
              'token':sessionStorage.getItem('token')
            }
          }).then(res => {
            const formdata = new FormData()
            formdata.append('file', req.file)
            formdata.append('token', res.data.data)
            formdata.append('key', keyname)
            // 
            axios.post(this.domain, formdata, config).then(res => {
              this.imageUrl = 'http://' + this.qiniuaddr + '/' + res.data.key
              this.imageUrl1 = 'http://' + this.qiniuaddr + '/' + res.data.key
              this.img = false;
              console.log(this.imageUrl)
            })
          })
        },
        // 
        beforeUpload(file) {
          const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
          const isLt2M = file.size / 1024 / 1024 < 2
          if (!isJPG) {
            this.$message.error(' JPG !')
          }
          if (!isLt2M) {
            this.$message.error(' 2MB!')
          }
          return isJPG && isLt2M
        },
        handleSearch:function(){
          axios({
            method: 'post',
            url: this.global.mPath + '/signin/querysigninprize',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'token':sessionStorage.getItem('token')
            }
          })
            .then((res) => {
                this.loading = false;//
                this.lottery = res.data.data;
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 500:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          });
        },
        adjustmentDialog:function(index,row){
          this.adjustmentVisible = true;
          this.adjustForm = Object.assign({}, row);
          this.imageUrl1 = this.adjustForm.iconUrl
        },
        adjustSubmit:function(){
          axios({
            method: 'post',
            url: this.global.mPath + '/signin/updatesigninprize',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              id:this.adjustForm.id,
              name:this.adjustForm.name,
              type:this.adjustForm.type,
              singleNum:this.adjustForm.singleNum,
              storeNum:this.adjustForm.storeNum,
              iconUrl:this.imageUrl1,
              prizeProb:this.adjustForm.prizeProb,
              firstPrizeProb:this.adjustForm.firstPrizeProb,
              overstep:this.adjustForm.overstep,
              token:sessionStorage.getItem('token')
            }
          })
            .then((res) => {
              if (res.data.success == true) {
                this.adjustmentVisible = false;
                this.$message.success({showClose: true, message: '', duration: 1500});
                this.handleSearch(1);
              } else {
                this.$message.error({showClose: true, message: err.toString(), duration: 2000});
              }
              },
            ).catch((e) => {
            this.loading = false;
            this.$message.error({showClose: true, message: '', duration: 2000});
          });
        },
        showAddDialog:function(){
          this.$confirm('', '', {
            type: 'warning'
          }).then(() => {
            axios({
              method: 'post',
              url: this.global.mPath + '/signin/releasesigninprize',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                'token':sessionStorage.getItem('token')
              }
            })
              .then((res) => {
                  if (res.data.success == true) {
                    this.tip3Visible = true;
                    this.$message.success({showClose: true, message: '', duration: 1500});
                    this.handleSearch(1);
                  } else {
                    if(res.data.msg == 'notEnough'){
                      this.tipVisible = true;
                    }else if(res.data.msg == 'PrizeProbOverstep'){
                      this.tip1Visible = true;
                    }else if(res.data.msg == 'FirstPrizeProbOverstep'){
                      this.tip2Visible = true;
                    }
                  }
                },
              ).catch((e) => {
              this.loading = false;
              this.$message.error({showClose: true, message: '', duration: 2000});
            });
          })
        },
        AddDialog:function(){
          this.addVisible = true;
        },
        publishSubmit:function(){
          axios({
            method: 'post',
            url: this.global.mPath + '/signin/addsigninprize',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              name:this.addForm.name,
              type:this.addForm.type,
              singleNum:this.addForm.singleNum,
              storeNum:this.addForm.storeNum,
              iconUrl:this.imageUrl,
              prizeProb:this.addForm.prizeProb,
              firstPrizeProb:this.addForm.firstPrizeProb,
              overstep:this.addForm.overstep,
              token:sessionStorage.getItem('token')
            }
          })
            .then((res) => {
                if (res.data.success == true) {
                  this.addVisible = false;
                  this.$message.success({showClose: true, message: '', duration: 1500});
                  this.handleSearch(1);
                } else {
                  if(res.data.msg == 'overstep'){
                    this.tip4Visible = true;
                  }else{
                    this.$message.error({showClose: true, message: err.toString(), duration: 2000});
                  }
                }
              },
            ).catch((e) => {
            this.loading = false;
            this.$message.error({showClose: true, message: '', duration: 2000});
          });
        },
        delBook:function(index,row){
          this.$confirm('', '', {
            type: 'warning'
          }).then(() => {
            axios({
              method: 'post',
              url: this.global.mPath + '/signin/deletesigninprizebyid',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                id: row.id,
                token:sessionStorage.getItem('token')
              }
            })
              .then((res) => {
                  if (res.data.success == true) {
                    this.addVisible = false;
                    this.$message.success({showClose: true, message: '', duration: 1500});
                    this.handleSearch(1);
                  } else {
                    this.$message.error({showClose: true, message: err.toString(), duration: 2000});
                  }
                },
              ).catch((e) => {
              this.loading = false;
              this.$message.error({showClose: true, message: '', duration: 2000});
            });
          })
        }
      },
      mounted(){
       this.handleSearch();
      },

    }
</script>

<style scoped>
  .toolbar{
    margin-top:30px;
  }
  .el-input{
    width:300px;
  }
 .avatar-uploader img{
   width:150px;
   height:100px;
 }
  .title{
    width: 400px;
    height: 43px;
    text-align: center;
    border-bottom: 1px solid #eee;
    margin: -50px 0 0 -20px;
    line-height:43px;
    font-size:20px;
  }
  .content{
    width: 290px;
    padding:55px;
    margin: 0 0 0 -20px;
    font-size:20px;
    border-bottom: 1px solid #eee;
  }
  .content1{
    width: 360px;
    padding: 55px 20px;
    margin: 0 0 0 -20px;
    font-size: 20px;
    border-bottom: 1px solid #eee;
  }

</style>



