<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0;margin-top:30px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.id" placeholder="ID" @keyup.enter.native="handleSearch()"></el-input>
        </el-form-item>
        <el-button type="primary" @click="handleSearch()"></el-button>
        <el-button type="primary" @click="addLimit()"></el-button>
      </el-form>
    </el-col>

    <!-- -->
    <el-table :data="vip" highlight-current-row @selection-change="selsChange"
              style="width: 100%;">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" width="40"></el-table-column>
      <el-table-column prop="id" label="ID" width="90"></el-table-column>
      <el-table-column prop="nickname" label="" width="100"></el-table-column>
      <el-table-column prop="gold" label="" width="120"></el-table-column>
      <el-table-column prop="score" label="" width="120"></el-table-column>
      <el-table-column prop="createTime" label="" width="100"></el-table-column>
      <el-table-column prop="other" label="">
        <template slot-scope="scope">
          <el-button type="danger" @click="del(scope.$index,scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="1" :total="total"
                     style="float:right;">
      </el-pagination>
    </el-col>

    <!---->
    <el-dialog title="" :visible.sync="addLimitVisible" :close-on-click-modal="false">
      <el-form :model="addLimitForm" label-width="100px" :rules="rules" class="demo-ruleForm">
        <el-form-item label="ID" prop="gold">
          <el-input v-model="addLimitForm.gold" auto-complete="off" style="width:217px;"></el-input>
        </el-form-item>
        <el-form-item label="" required>
          <el-select v-model="time" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="gold">
          <el-input v-model="addLimitForm.gold" auto-complete="off" style="width:217px;"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="sure"></el-button>
          <el-button type="primary" @click.native="addLimitVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

  </el-row>
</template>

<script>
    export default {
        name: "LimitLogin",
        data(){
          return{
            filters:{},
            total:0,
            addLimitVisible:false,
            addLimitForm:{},
            options:[
              {value:'1'},
              {value:'7'},
              {value:'30'},
              {value:''},
              {value:'1'},
              {value:''},
            ],
            time:'',
            rules: {
              gold: [
                {required: true, message: 'ID', trigger: 'blur'}
              ],
              score: [
                {required: true, message: '', trigger: 'blur'}
              ],
            },
          }
        },
        methods:{
          handleSearch(){},
          addLimit(){
            this.addLimitVisible = true;
          },
          handleCurrentChange(val){},
          sure(){},
          selsChange(){}
        }
    }
</script>

<style scoped>

</style>



