<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><b></b></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <div class="title"></div>
    <div class="number"></div>
    <el-row :gutter="20" style="margin:10px 0 0 30px;">
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters.amount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters.vipAmount}}</div>
        </div>
      </el-col>
    </el-row>
    <div class="sks"></div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-top:20px;">
        <el-select v-model="value" clearable placeholder="" style="margin-left:40px;" @change="handleSearch(value)">
          <el-option
            v-for="item in month"
            :key="item.value"
            :label="item.value"
            :value="item.value">
          </el-option>
        </el-select>
    </el-col>
    <el-row :gutter="20" style="margin:70px 0 0 30px;">
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters.memberRiAmount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters.memberZhouAmount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters.memberYueAmount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters.memberJiAmount}}</div>
        </div>
      </el-col>
    </el-row>
    <hr>
    <div class="title"></div>
    <div class="number"></div>
    <el-row :gutter="20" style="margin:10px 0 0 30px;">
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters1.seniorAmount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters1.juniorAmount}}</div>
        </div>
      </el-col>
    </el-row>
    <div class="sks"></div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;margin-top:20px;">
        <el-select v-model="value1" placeholder="" clearable style="margin-left:40px;" @change="handleSearch1(value1)">
          <el-option
            v-for="item in month"
            :key="item.value"
            :label="item.value"
            :value="item.value">
          </el-option>
        </el-select>
    </el-col>
    <el-row :gutter="20" style="margin:70px 0 0 30px;">
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters1.agentRiAmount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters1.agentZhouAmount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters1.agentYueAmount}}</div>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="grid-content bg-purple">
          <div class="type"></div>
          <div class="num">{{filters1.agentJiAmount}}</div>
        </div>
      </el-col>
    </el-row>
  </el-row>
</template>

<script>
    import axios from 'axios'
    export default {
        name: "Index",
        data(){
          return{
            month:[
              {value:'1'},
              {value:'2'},
              {value:'3'},
              {value:'4'},
              {value:'5'},
              {value:'6'},
              {value:'7'},
              {value:'8'},
              {value:'9'},
              {value:'10'},
              {value:'11'},
              {value:'12'},
            ],
            filters:[],
            filters1:[],
            value:'',
            value1:''
          }
        },
      methods:{
          handleSearch(month){
            if(!month){
              var myDate = new Date();
              this.value = myDate.getMonth()+1 +'';
            }
            if(month == '1'){
              month = 1;
            }
            if(month == '2'){
              month = 2;
            }
            if(month == '3'){
              month = 3;
            }
            if(month == '4'){
              month = 4;
            }
            if(month == '5'){
              month = 5;
            }
            if(month == '6'){
              month = 6;
            }
            if(month == '7'){
              month = 7
            } if(month == '8'){
              month = 8;
            }
            if(month == '9'){
              month = 9;
            }
            if(month == '10'){
              month = 10;
            }
            if(month == '11'){
              month = 11;
            }
            if(month == '12'){
              month = 12;
            }
            axios({
              method: 'post',
              url: this.global.mPath + '/login/memberindex',
              headers: {
                'Content-type': 'application/x-www-form-urlencoded'
              },
              params: {
                'month': month,
                'token':sessionStorage.getItem('token')
              }
            })
              .then((res) => {
                  console.log(res.data)
                  this.filters = res.data.data;
                },
              ).catch((e) => {
              if (e && e.response) {
                switch (e.response.status) {
                  case 504:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break
                  case 500:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    this.loading = false;//
                    break
                  case 405:
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'warning'
                    });
                    break
                }
              }
            });
          },
          handleSearch1(month){
          if(!month){
            var myDate = new Date();
            this.value1 = myDate.getMonth()+1+'';
          }
          if(month == '1'){
            month = 1;
          }
          if(month == '2'){
            month = 2;
          }
          if(month == '3'){
            month = 3;
          }
          if(month == '4'){
            month = 4;
          }
          if(month == '5'){
            month = 5;
          }
          if(month == '6'){
            month = 6;
          }
          if(month == '7'){
            month = 7
          } if(month == '8'){
            month = 8;
          }
          if(month == '9'){
            month = 9;
          }
          if(month == '10'){
            month = 10;
          }
          if(month == '11'){
            month = 11;
          }
          if(month == '12'){
            month = 12;
          }
          axios({
            method: 'post',
            url: this.global.mPath + '/login/agentindex',
            headers: {
              'Content-type': 'application/x-www-form-urlencoded'
            },
            params: {
              'month': month,
              'token':sessionStorage.getItem('token')
            }
          })
            .then((res) => {
                console.log(res.data)
                this.filters1 = res.data.data;
              },
            ).catch((e) => {
            if (e && e.response) {
              switch (e.response.status) {
                case 504:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 500:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  this.loading = false;//
                  break
                case 405:
                  this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                  });
                  break
              }
            }
          });
        }
      },
      mounted(){
          this.handleSearch('')
          this.handleSearch1('')
      }
    }
</script>

<style scoped>
  .title{
    margin-top:30px;
    padding-bottom:10px;
    border-bottom:1px solid #eee;
  }
  .number{
    margin:10px 0 0 50px;
  }
  .sks{
    margin-top:20px;
    padding:10px 0 0 50px;
    border-top:1px solid #eee;
  }
  hr{
    border: 2px solid #000;
    margin-top:40px;
  }
  .bg-purple{
    width:200px;
    height:100px;
    text-align: center;
    background-color: #eee;
    border-radius: 5px;
    border:1px solid #eee;
  }
  .bg-purple div:nth-child(1){
    margin-top:20px;
    font-size:20px;
  }
  .bg-purple div:nth-child(2){
    margin-top:10px;
  }
</style>



